			flex-wrap: nowrap;
			border-bottom: 1px solid #dedede;

			&:last-child {
				border-bottom: none;
			}

			.img {
				width: calc(30vw - 70rpx);
				height: calc(27vw);
				border-radius: 20rpx;
				// overflow: hidden;
				flex-shrink: 0;
				margin-right: 20upx;

				image {
					width: calc(30vw - 70rpx);
					height: calc(27vw);
				}
			}

			.info {
				width: 100%;
				height: calc(30vw);
				overflow: hidden;
				flex-wrap: wrap;
				align-content: space-between;
				position: relative;

				.name {
					width: 100%;
					font-size: 30rpx;
					display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 2;
					overflow: hidden;
					font-weight: bold;
					line-height: 40rpx;
					height: 80rpx;
				}

				.series {
					.series1 {
						color: rgb(153, 153, 153);
						font-weight: 500;
						font-size: 25rpx;
					}
				}

				.borrowTime {
					.borrowTime1 {
						color: rgb(153, 153, 153);
						font-weight: 500;
						font-size: 25rpx;
					}
				}
			}

			.countDownPanel {
				width: 130rpx;
				margin-top: 80rpx;

				.countDownimg {
					float: left;

					image {
						width: 40rpx;
						height: 40rpx;
						margin-top: 5rpx;
					}
				}

				.countDownTime {
					float: left;
					color: rgb(153, 153, 153);
				}
			}

			.cancelbooks {
				width: 130rpx;
				background-color: #FACC3F;
				height: 50rpx;
				line-height: 50rpx;
				font-size: 26rpx;
				text-align: center;
				color: white;
				border-radius: 10rpx;
			}

			.returnbook {
				width: 130rpx;
				background-color: #53D776;
				height: 50rpx;
				line-height: 50rpx;
				font-size: 26rpx;
				text-align: center;
				color: white;
				border-radius: 10rpx;
				margin-top: 100rpx;
			}

			.cancelReturnBook {
				width: 130rpx;
				background-color: #FE613D;
				height: 50rpx;
				line-height: 50rpx;
				font-size: 26rpx;
				text-align: center;
				color: white;
				border-radius: 10rpx;
			}
		}

	}
</style>